<template>
  <section class="flex">
    <div v-if="isHiddenNav" class="hidden lg:block mr-8"></div>
    <nav class="hidden lg:block mr-8" v-else>
      <slot name="router"></slot>
      <div class="flex flex-col mt-72 ml-10">
        <router-link
          v-for="{ path, name } in footerLinks"
          :key="name"
          :to="path"
          class="text-link hover:text-main-red mb-2.5"
        >
          {{ name }}
        </router-link>
      </div>
    </nav>
    <div class="flex-1 px-4 lg:px-0 lg:pr-10 overflow-auto flex flex-col">
      <NavbarLink />
      <div class="flex flex-col lg:flex-row gap-2.5 mb-5 lg:mb-20">
        <div
          class="h-16 lg:h-40 lg:w-1/2 bg-gray-100 flex items-center justify-center"
        >
          ad
        </div>
        <div
          class="h-16 lg:h-40 lg:w-1/2 bg-gray-100 flex items-center justify-center"
        >
          ad
        </div>
      </div>
      <div class="flex-1">
        <slot></slot>
      </div>
      <div class="hidden lg:flex gap-5 mt-8 w-full justify-between">
        <div class="w-80 h-40 bg-gray-100 flex items-center justify-center">
          ad
        </div>
        <div class="w-80 h-40 bg-gray-100 flex items-center justify-center">
          ad
        </div>
        <div class="w-80 h-40 bg-gray-100 flex items-center justify-center">
          ad
        </div>
        <div class="w-80 h-40 bg-gray-100 flex items-center justify-center">
          ad
        </div>
        <div class="w-80 h-40 bg-gray-100 flex items-center justify-center">
          ad
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import { mapGetters } from "vuex";
import NavbarLink from "@/components/NavbarLink";
export default {
  components: {
    NavbarLink,
  },
  data() {
    return {
      isHiddenNav: false,
      footerLinks: [
        { path: "/system/policy", name: "政策說明" },
        { path: "/system/service", name: "服務條款" },
        { path: "/system/privacy", name: "隱私權政策" },
        { path: "/system/disclaimer", name: "免責聲明" },
        { path: "/system/link", name: "友情連結" },
        { path: "/system", name: "聯絡我們" },
      ],
    };
  },
  computed: {
    ...mapGetters(["isLogin"]),
  },
  watch: {
    $route: {
      immediate: true,
      handler: function (value) {
        const isMember = value.matched.find((item) => item.name === "Member");
        if (isMember && !this.isLogin) {
          this.isHiddenNav = true;
          return;
        }
        this.isHiddenNav = false;
      },
    },
  },
};
</script>
